import React, { Component } from 'react';
import TestHome from './TestHome';




class Test extends Component {

   state = {
    num:"2",

   };


  render(){
      let {num} = this.state;
  return (
    <div>
        测试
        
        <p>{num}</p>
        <input  value={this.state.num}
                  onChange={event=>{this.setState({num:event.target.value})}}/>
        <br/>
        <br/>
        <TestHome num = {num}/>

    </div>
  );
  }
}


export default Test;
